@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&family=Noto+Sans+KR:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body{
  overflow-x: hidden;
}

html{
  scroll-behavior: smooth;
}

@-webkit-keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

@keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

@-webkit-keyframes big {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  50% {box-shadow: 0 0 15px 0px rgb(255, 217, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
}

@keyframes big {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  50% {box-shadow: 0 0 15px 0px rgb(255, 217, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
}


body {
  font-family: 'Noto Sans KR', sans-serif;
  background-color: rgba(172, 255, 47, 0.103);
}

.App {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
}

/* start button */
.gotrip {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-family: 'East Sea Dokdo', cursive;;
  font-size: 15vmin;
  padding: 10px 25px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  z-index: 100;
}

/* day button */
.daybuttons {
  display: none;
}

.daybuttons.active {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: space-around;
          align-content: space-around;
  margin: auto;
  z-index: 99;
}

.btn {
  font-family: 'East Sea Dokdo', cursive;;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-size: 10vmin;
  padding: 15px 30px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  margin: 20px 0;
}


/* main contents */
.container {
  display: none;
}

.container.active {
  display: block;
  transition: .3s ease-in-out;
  width: 100vw;
}


/* left side bar */
.leftnavi{
  font-family: 'East Sea Dokdo', cursive;;
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 100vh;
  background-color: rgba(136, 182, 11, 0.192);
  overflow: hidden;
  display: block;
  transition: .3s ease-in-out;
  z-index: 3;
}

.leftnavi a{
  height: 20px;
  text-align: center;
  border-radius: 5px;
  margin: 40px 30px;
  text-decoration: none;
  font-size: 25px;
  color: #353333;
  display: block;
  overflow: hidden;
  border-bottom: 1px rgba(0, 0, 0, 0.205) solid;
  padding-bottom: 10px;
  text-shadow: 0 0 10px rgba(172, 255, 47, 0.541);
}


/* top side bar */
.navibutton {
  display: none;
  position: fixed;
  background-image: url("https://image.flaticon.com/icons/png/512/660/660376.png");
  background-color: white;
  border-radius: 100%;
  margin: 10px;
  width: 30px;
  height: 30px;
  background-size: cover;
  top:0px;
  left: 0px;
}

.navibutton.on {
  display: block;
}

.topnavi {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 8px;
  border-radius: 10px;
  background-color: white;
  z-index: 3;
}

.jejumap {
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
  box-shadow: 0 0 5px 0px rgba(0, 128, 0, 0.342);
  transition: 1s;
}

.gotrip.active {
  display: none;
}

@-webkit-keyframes leftright {
  0% {-webkit-transform: translateY(-30%);transform: translateY(-30%); opacity: 0;}
  80% { -webkit-transform: translateY(20%); transform: translateY(20%)}
  100%  {-webkit-transform: translateY(0);transform: translateY(0); opacity: 1;}
}

@keyframes leftright {
  0% {-webkit-transform: translateY(-30%);transform: translateY(-30%); opacity: 0;}
  80% { -webkit-transform: translateY(20%); transform: translateY(20%)}
  100%  {-webkit-transform: translateY(0);transform: translateY(0); opacity: 1;}
}

/* list container */
.list{
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-animation: leftright .5s ease-in-out;
          animation: leftright .5s ease-in-out;
}

.list_li {
  margin : 8px 10px;
  border-radius: 10px;
  padding : 10px 5px;
  background-color: white;
  box-shadow: 0 0 5px 0px rgba(128, 128, 128, 0.514);
  cursor: pointer;
}


.list_li.active {
  -webkit-animation: big 5s infinite ;
          animation: big 5s infinite ;
  transition: 1s;
}

.list_ul {
  pointer-events: none;
}

.list_ul li {
  pointer-events: none;
  padding: 5px;
}

.list_ul li:last-child {
  pointer-events: all;
}
.list_ul li:nth-child(2),
.list_ul li:nth-child(3),
.list_ul li:nth-child(4),
.list_ul li:nth-child(5){
  display: none;
}

.list_li.active li:nth-child(2),
.list_li.active li:nth-child(4),
.list_li.active li:nth-child(5){
  display: -webkit-flex;
  display: flex;
  border-top:1px solid rgba(128, 128, 128, 0.356);
  font-size: 13px;
}

.list_num{
  font-family: 'Poor Story', cursive;
  color: #353333;
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  font-weight: bolder;
}

.list_name{
  color: black;
  font-size:18px;
  margin: auto;
  
}

.list_ul a{
  color: black;
}

.daylist {
  margin: 10px 0;
  font-weight: bolder;
  font-family: 'Poor Story', cursive;
  font-size: 25px;
  text-align: center;
  padding: 10px 0;
  border-top: 1px solid rgba(128, 128, 128, 0.521);
  border-bottom: 1px solid rgba(128, 128, 128, 0.452);
}
@media screen and (min-width:600px) {
  body {
    width: 500px;
    margin: 0 50vw;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}
